import React from 'react'
import 'antd/dist/antd.css'
import { Input, Button, List } from "antd"
import store from '../store/index'

import {inputChangeAction, addAction, deleteACtion} from '../store/actionContent'
class TodoList extends React.Component {
  constructor(props) {
    super(props);
    this.state = store.getState();
    this.changeState = this.changeState.bind(this)
    store.subscribe(this.changeState)
  }

  render() {
    return (
      <div style={{ margin: "10px" }}>
        <div>
          <Input
            placeholder={this.state.value}
            style={{ width: "250px", marginRight: "10px" }}
            onChange={this.inputChange.bind(this)}
            value = {this.state.value}
          />
          <Button type="primary" onClick = {this.add.bind(this)}>增加</Button>
        </div>
        <div style={{ margin: "10px", width: "300px" }}>
          <List
            bordered
            dataSource={this.state.list}
            renderItem={(item, index) => <List.Item>{item} <Button type = 'primary' onClick = {this.delete.bind(this,index)}>Delete</Button> </List.Item>}
          />
        </div>
      </div>
    );
  }

  changeState(){
      this.setState(store.getState())
  }

  inputChange(e){
    const action = inputChangeAction(e)
    store.dispatch(action)
  }

  add(){
      const action = addAction(this.state.value)
      store.dispatch(action)
  }

  delete(index){
      const action = deleteACtion(index)
      store.dispatch(action)
  }
}

export default TodoList;