import {INPUT_CHANGE, ADD_VALUE, DELETE_VALUE} from './actionTypes'

export const inputChangeAction = (e) => {
    return(
        {
            type: INPUT_CHANGE,
            value: e.target.value
        }
    )
}

export const addAction = (value) =>{
    return(
        {
            type: ADD_VALUE,
            value: value
        }
    )
}

export const deleteACtion = (index) => {
    return(
        {
            type: DELETE_VALUE,
            value: index
        }
    )
}